.team_member {
  .photo {
    float: left;
    max-width: 13rem;
    padding: 0 1.2rem .8rem 0;
    @media (max-width: 979px) {
      width: 100%;
      max-width: unset;

      img {
        width: 13rem;
      }
    }
    @media (max-width: 480px) {}
  }
  @extend %clearfix;

  .name {
    text-transform: uppercase;
    margin-bottom: 1rem;
    color: $theme1;

    span {
      &:last-child {
        &:before {
          content: ' | ';
        }
      }
    }
  }

  .description {
    font-size: .9em;
    line-height: 1.7;
  }
}

.leadership-list {
  .team_member {
    padding-top: 2rem;
    padding-bottom: 2rem;

    &:after {
      content: '';
      display: table;
      clear: both;
    }
    @media (max-width: 979px) {
      padding-bottom: 0;
    }
  }

  .row {
    &:after {
      margin-bottom: 1rem;
      display: block;
      content: '';
      height: .05rem;
      min-height: 1px;
      width: 100%;
      background: $theme2;
      @media (max-width: 979px) {
        display: none;
      }
    }

    &:last-child {
      &:after {
        display: none;
      }
    }
  }
}
