section.testimonials {

  padding: 5rem 0;
  background: $light1;

  h3 {
    margin-bottom: 1em;
    color: $theme1;
  }

  // .container {
  .testimonials-body {
    display: flex;
    flex-direction: row;
  }

  .testimonials-list {
    width: 55%;
  }

  .testimonials-mark {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 45%;
    padding: 1rem 0;

    .number {
      /* Style for "4.3" */
      // width: 15.188rem;
      // height: 9.75rem;
      color: #7d7e7f;
      // color: $dark;
      font-family: 'Open Sans', sans-serif;
      font-size: 13.313rem;
      font-weight: 300;
      font-style: italic;
      /* Text style for "4.3" */
      letter-spacing: -1.02rem;

      // font-size: 11rem;
      // font-style: italic;
      line-height: 1;
      // font-weight: 300;

      * {
        padding: 0;
        margin: 0;
      }
    }

    .stars {
      font-size: 2rem;
      color: $theme1;
    }
  }

  .testimonials-list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .testimonial-item {
    width: 100%;
    background: $light;
    padding: 1rem;
    margin-bottom: 3rem;

    position: relative;

    &:after {
      font-size: 2.8rem;
      line-height: 1;
      text-align: center;
      content: '. . . .';
      display: block;
      width: 100%;
      position: absolute;
      left: 0;
      color: $theme1;
      top: 100%;
      margin-top: -1rem;
    }

    &:nth-last-child(2) {
      margin-bottom: 1rem;
      &:after {
        display: none;
      }
    }

    &:last-child {}

    .t-text {}

    .signature {
      text-transform: uppercase;
      color: $theme1;
      font-weight: 600;
      margin-top: .5rem;

      span {
        &:before {
          display: inline;
          content: ' | ';
        }
      }
    }
  }
  @media (max-width: 979px) {
    .testimonials-body {
      flex-direction: column-reverse;
    }

    .testimonials-list {
      width: 100%;
    }

    .testimonials-mark {
      width: 100%;

      .number {
        font-size: 9rem;
      }
    }
  }
}
