
/* buttons */

%btn {
  display: inline-block;
  text-align: center;
  width: unset;
  font-size: .85rem;
  font-weight: 400;
  text-transform: uppercase;
  border: 3px solid transparent;
  padding: .6rem 1.4rem;
  color: transparent;
  background: transparent;
  cursor: pointer;
  
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
  letter-spacing: 0.2em;

  &:active,
  &:focus {
    text-decoration: none;
    outline: none;
  }

  &:hover {
    background: transparent;
    color: transparent;
    border-color: transparent;
    text-decoration: none;
    opacity: 1;
  }
}

.btn {
  &-custom {
    @extend %btn;
    color: $theme1;
    background: $light;
    border-color: $light;

    &:active,
    &:focus {
      color: $theme1;
      background: rgba($light,0.75);
    }

    &:hover {
      background: rgba($light,0.75);
      color: $theme1;
      border-color: $light;
    }

    &_d {
      @extend %btn;
      color: $light;
      background: $dark;
      border-color: $dark;

      &:active,
      &:focus {
        color: $light;
        background: rgba($dark,0.75);
      }

      &:hover {
        background: $dark;
        background: rgba($dark,0.75);
        color: $light;
        border-color: $dark;
      }
    }
  }

  &-empty {
    @extend %btn;
    color: $light;
    border-color: $light;
    background: transparent;

    &:active,
    &:focus {
      color: $light;
      background: rgba($light,0.3);
    }

    &:hover {
      color: $light;
      border-color: $light;
      background: rgba($light,0.3);
    }

    &_d {
      @extend %btn;
      color: $dark;
      border-color: $dark;
      background: transparent;

      &:active,
      &:focus {
        color: $dark;
        background: rgba($dark,0.3);
      }

      &:hover {
        color: $dark;
        border-color: $dark;
        background: rgba($dark,0.3);
      }
    }
  }
}
