header {
  background: rgba($theme1, .8);
  color: $light;
  position: absolute;
  width: calc(100% - .1px);
  z-index: 10;
  min-height: 5rem;

  .container {
    height: 100%;
    display: flex;
    justify-content: space-between;

    > * {
      margin-top: auto;
      margin-bottom: auto;
    }
  }

  .header-logo {

    background: $theme2;
    margin-right: auto;
    max-width: calc(100% - 6rem);
    padding: 1rem 2rem;
    height: 100%;
    display: flex;
    align-items: center;

    img {
      margin-top: .5rem;
    }
    position: relative;

    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      top: 100%;
      left: 0;
      background: $theme2;
      height: .5rem;
    }
  }
}

.nav-bars {
  cursor: pointer;
  margin-left: 1rem;
  display: none;
  position: relative;
  z-index: 10;

  span {
    display: block;
    width: 43px;
    height: 5px;
    margin-bottom: 6px;
    background: $light;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.navigation-list {
  margin-left: auto;
  margin-right: 0;
  display: flex;
  align-items: center;

  li,
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  a {
    color: $light;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 1.42;
    text-transform: uppercase;
    letter-spacing: .1em;

    &:active,
    &:focus {
      color: $light;
    }

    &:hover {
      opacity: 1;
    }
  }

  ul {
    li {
      a {
        display: block;
        padding: .5rem 1rem;
        @media (max-width: 1199px) {
          padding: .5rem .7rem;
        }

        &:after {
          opacity: .3;
          content: '';
          display: block;
          width: 0;
          margin-left: 50%;
          height: 2px;
          min-height: 1.5px;
          background: rgba($light,1);
          transition: ease-in-out all .15s;
        }
      }
    }
  }
  @media (min-width: 992px) {
     @media (max-width: 1199px){
      ul {
        li {
          a {}
        }
      }
    }

    > ul {
      display: flex;
      align-items: center;

      > li {
        position: relative;

        > a {
          max-width: 14rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;

          text-align: center;
          text-decoration: none;
        }

        a {
          font-size: .75rem;

          // padding: .5rem .7rem;
        }

        li {
          a {
            &:hover {
              text-decoration: underline;
            }
          }
        }

        &:hover {
          > ul {
            opacity: 1;
            pointer-events: all;
          }

          > a {
            &:after {
              opacity: 1;
              margin-left: 0;
              width: 100%;
            }
          }
        }

        > ul {
          &:before {
            content: '';
            display: block;
            width: 100%;
            height: .7rem;
            background: transparent;
            position: absolute;
            left: 0;
            bottom: 100%;
          }
          min-width: 10rem;
          pointer-events: none;
          opacity: 0;
          position: absolute;
          top: calc(100% + .7rem);
          left: 0;
          padding: .7rem 1rem;
          background: rgba($theme1,.5);
          flex-direction: column;

          > li {
            > a {
              padding: .25rem 0;
            }
          }
        }
      }
    }
  }
  @media (max-width: 991px) {
    .active & {
      opacity: 1;
      pointer-events: all;
    }
    display: flex;
    flex-direction: column;
    background: $theme1;
    position: absolute;
    top: 0;
    z-index: 9;
    padding: 1rem 2rem 2rem;
    @media (min-width: 767px) {
      right: 13%;

      &:before {
        position: absolute;
        content: '';
        display: block;
        height: 100%;
        width: 12.8vw;
        background: $theme1;
        top: 0;
        left: 100%;
      }
    }
    @media (max-width: 767px) {
      right: 0;
      padding-right: 4rem;
    }

    //submenu
    > ul {
      > li {
        a {
          padding-left: 0;

          max-width: 20rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        > ul {
          padding-left: .7rem;

          * {
            line-height: 1.3;
          }
        }
      }
    }
    //hover
    ul {
      li {
        a {
          text-decoration: none;

          &:active,
          &:focus,
          &:hover {
            &:after {
              opacity: 1;
              margin-left: 0;
              width: 100%;
            }
          }
        }
      }
    }

    .btn-custom,
    .btn-custom_d,
    .btn-empty,
    .btn-empty_d {
      width: 100%;
      margin-top: .5rem;
    }
  }
}
@media (max-width: 991px) {
  .nav-bars {
    display: block;
  }

  .navigation-list {
    opacity: 0;
    pointer-events: none;
  }
}

