.header_bg {
  display: block;
  width: 100%;
  height: 8rem;
  @extend %line;
  position: relative;
  width: 100%;
  z-index: 1;

  &:after {
    z-index: 3;
    bottom: .5rem;
  }

  &:before {
    z-index: 1;
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url($imgPath + 'bg_houses.jpg');
    background-position: 50% 50%;
    opacity: .5;
  }
  background-color: rgba($theme1, .4);
  background-color: rgba($theme1, 1);
}
