section.phone_content {
  padding: 6rem 0;
  position: relative;

  .bg-image {
    @extend %bg-image;
    background: transparent;
    @media (max-width: 767px) {

      left: -20%;
      right: 70%;

      img {
        object-position: 50% 20%;
      }
    }

    img {
      object-position: 50% 20%;
      object-fit: contain;
    }

    .container {
      height: 100%;

      .content-divide {
        height: 100%;
        width: 100%;
        padding-right: 50%;
        @media (max-width: 767px) {
          padding-right: 0;
        }

      }

      .content-hold {
        height: 100%;

        position: relative;
      }
    }
  }

  h3 {

    margin-bottom: .6rem;
    color: $dark;
  }

  .phone_content-content {
    @media (max-width: 767px) {
      padding-left: 20%;

    }

    width: 100%;
    padding-left: 42%;
  }
}

section.categories {
  min-height: 650px;
  display: flex;
  position: relative;

  .bg-image {
    background: $theme1;
    @extend %bg-image;

    img {
      opacity: .15;
    }
  }

  .container {
    display: flex;
    @media (max-width: 1000px) {
      flex-direction: column;
    }
  }

  .nav-tabs {
    position: relative;

    &:after,
    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 1rem;
      position: absolute;
      left: 0;
      background: $theme2;
    }

    &:before {
      top: 100%;
    }

    &:after {
      bottom: 100%;
    }
    border: 0 solid;

    flex-shrink: 0;
    width: 20rem;
    @media (min-width: 980px) and (max-width: 1199px) {
      width: 15rem;
    }

    background: $theme2;
    padding: 2rem 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    li {
      box-sizing: content-box;
      width: 100%;
      text-align: center;
      margin: 0;
      padding: 0;
      border-bottom: .3rem solid $light;

      &:last-child {
        border: 0 solid;
      }

      a {
        cursor: pointer;

        img {
          margin-right: .3rem;
        }
        border-radius: 0;
        width: unset;
        transform: all .2s ease-in;
        display: inline-block;
        text-transform: uppercase;
        display: block;
        border: 0 solid;
        height: 6rem;
        line-height: 6rem;
        margin: 0;
        padding: 0;
        color: $light;

        &:active,
        &:focus,
        &:hover {
          outline: none;
          width: 100%;
          background: rgba(#fff,.05);
        }
      }

      &.active {
        a {
          border: 0 solid;
          color: $light;
          background: rgba(#fff,.1);
          cursor: pointer;

          &:hover {
            background: rgba(#fff,.05);
          }
        }
      }
    }
  }

  .tab-content {
    background: rgba(#fff,0.8);
    margin: 2rem 0;
    padding: 2rem 2.5rem;
    flex-grow: 1;

    display: flex;
    align-items: center;
  }

  .form-description {
    h5 {
      text-transform: uppercase;
      width: 100%;
      flex-grow: 1;
      margin-bottom: .5rem;
    }
    display: flex;
    flex-wrap: wrap;

    .contact-item {
      min-width: calc((100% - 2rem)/3);
      margin-right: 1rem;

      &:nth-child(4n) {
        margin-right: 0;
      }

      &.long {
        width: 65%;
      }
      position: relative;
      padding-left: 1.5rem;
      margin-top: .5rem;
      margin-bottom: .5rem;
      color: $dark;
      line-height: 1.4;

      &:before {
        position: absolute;
        left: 0;
        top: .25rem;
        content: '';
        display: block;
        width: .8rem;
        height: .8rem;
        background: $theme1;
      }
    }
  }

  .tab-categories {
    .content-block-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      margin-bottom: -1rem;
      margin-top: -1rem;

    }

    .categories-textitem,
    .row-item {
      * {
        position: static;
      }

      .edit-group {
        position: absolute;
      }
      margin-top: 1rem;
      margin-bottom: 1rem;
      width: 45%;
      @media (max-width: 767px) {
        width: 85%;
      }

      position: relative;
      padding-left: 3rem;

      img {
        width: 2rem;
        left: 0;
        top: .3rem;
        position: absolute;
      }

      h6 {
        text-transform: uppercase;
      }

      a {
        text-transform: uppercase;
        font-weight: 600;
        color: $theme1;
      }
    }
  }
  @extend %line;
  @extend %line_before;

  &:before {
    z-index: 0;
    top: 1.8rem;
  }

  &:after {
    z-index: -1;
    bottom: 2rem;
  }
  @media (max-width: 1000px) {
    .nav-tabs {
      &:after,
      &:before {
        height: 100%;
        width: .5rem;
        top: 0;
        right: unset;
        left: unset;
        bottom: unset;
      }

      &:before {
        left: 100%;
      }

      &:after {
        right: 100%;
      }
      width: unset;
      padding: 1rem;
      margin: 1rem 0 -2rem;

      li {
        a {
          height: unset;
          line-height: 1;
          margin: 0;
          padding: 1rem;
        }
      }
    }

    &:after,
    &:before {
      display: none;
    }
  }

}
