.branch-team {
  padding-top: 4rem;
  padding-bottom: 6rem;

  .team-list {
    .slick-track{
      margin: 0;
    }
    // display: flex;
    // flex-direction: row;
    // list-style: none;
    margin: 1rem -1rem 0;
    padding: 0;

    // li {
    //   margin: 0;
    //   padding: 0;
    // }

    // justify-content: space-between;

    .team-member {
      padding: 0 .6rem;

      .square-box {
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .name {
        text-transform: uppercase;
        color: $theme1;
        font-weight: 700;
        line-height: 1.3;
        margin-top: .3rem;
        font-size: .85rem;
        // white-space: nowrap;

        span {
          &:before {
            content: ' | ';
          }

          &:first-child {
            &:before {
              display: none;
            }
          }
        }
      }
    }

    .slick-dots {
      bottom: -2rem;

      li {
        button {
          &:before {
            display: none;
          }
          margin: 0;
          padding: 0;
          font-size: 0;
          background: rgba($theme1,.2);
          border: 3px solid $theme2;
          border-radius: 50%;
          color: transparent;
          transition: all .1s ease-in-out;

          &:hover {
            background: rgba($theme1,.4);
          }

        }

        &.slick-active {
          button {
            background: rgba($theme2, .6);
          }
        }
      }
    }

    .slick-arrow {
      height: 100%;

      //appearance
      opacity: .5;
      z-index: 1;
      width: unset;
      padding: 0 1rem;

      &.slick-prev {
        margin-left: -2rem;
        left: 0;

        &:before {
          content: '<';
        }
      }

      &.slick-next {
        margin-right: -2rem;
        right: 0;

        &:before {
          content: '>';
        }
      }

      &.slick-disabled {
        opacity: .3;
      }

      &:hover {
        opacity: .7;
      }

      &:before {
        color: $theme1;
        font-size: 3rem;
        font-family: 'Open Sans', sans-serif;
        font-weight: 700;
      }
      @media (max-width: 900px) {
        height: unset;
        width: unset;
        top: unset;
        bottom: -4rem;
        // transform: translate(0%, 0%);

        &.slick-prev {
          margin: 0;
          transform: translate(-120%, 0%);
          left: 50%;
        }

        &.slick-next {
          margin: 0;
          transform: translate(120%, 0%);
          right: 50%;
        }
      }
    }
  }
}
