section.cta {
  position: relative;
  z-index: 1;

  .bg-image {
    @extend %bg-image;
    background: $theme1;

    img {
      opacity: .25;
    }
  }
  padding: 4.5rem 0;
  color: $light;

  .cta-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 767px) {
      flex-direction: column;
    }
  }

  .cta-text {
    font-size: 2.8rem;
    margin: auto;
    text-align: center;
    padding: 0 1rem;

    h1 {
      color: $light;
    }
  }

  .btn-empty {
    padding: .8rem;
    min-width: 10rem;
    @media (max-width: 767px) {
      margin-top: 2rem;
      transform: scale(1.2);
    }
  }
}

footer {
  // .footer-logo {}

  background: $theme2;
  color: $light;
  padding: 6rem 0 2rem;
  @extend %line;
  position: relative;

  li,
  ul {

    list-style: none;
    margin: 0;
    padding: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $light;
    text-transform: uppercase;
    margin-bottom: .5em;
  }

  &:after {
    top: 1rem;
  }

  .footer-presenter {
    em,
    i {
      opacity: .8;
      font-size: 1.1em;
    }
    display: flex;
    justify-content: space-between;
    align-items: center;

    .socials {
      margin-left: auto;
      margin-right: 2rem;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: flex-start;

      .socials {
        margin-left: 0;
      }

      > * {
        margin-bottom: .3rem;
      }
    }

    .phone {
      .socialicon,
      em {
        margin-right: .3rem;
      }
      display: flex;
      align-items: center;

      a {
        display: flex;
        align-items: center;
      }
    }
  }

  .footer-contact {
    display: flex;
    @media (max-width: 979px) {
      flex-wrap: wrap;
    }

    .nmls {
      margin-right: auto;
      width: 70%;
      order: 1;
      margin-top: 1rem;
      @media (min-width: 992px) {
        margin-top: 0;
        order: -1;
        width: calc(100%/4);
      }
      // @media (max-width: 979px) {
      //   width: calc(100%/2.5);
      //   order: -1;
      //   margin-bottom: 1rem;
      // }
      // @media (max-width: 767px) {
      //   width: calc(100%/1.5);
      // }
    }
  }

  .footer-info {
    text-align: center;
    font-size: .9rem;
    margin-top: .5rem;

    img {
      display: block;
      margin: 0 auto;
      padding: 1rem;
      max-width: 5rem;
      max-height: 5rem;
      box-sizing: content-box;
    }
  }

  .footer-navigation {
    width: 100%;
    @media (min-width: 992px) {
      width: 75%;
    }

    margin-right: -.2rem;

    > ul {
      width: 100%;
      display: flex;
      flex-wrap: wrap;

      > li {
        margin-right: 1rem;
      }
      justify-content: flex-start;
      @media (min-width: 992px) {
        justify-content: flex-end;
        > li {
          margin-right: 0;
          margin-left: 1rem;
        }
      }
      @media (max-width: 480px) {
        flex-wrap: nowrap;
        flex-direction: column;

        
      }

      > li {
        a {
          padding: .2rem;
        }

        > a {
          font-weight: 600;

          color: $light;
          font-family: 'Open Sans', sans-serif;
          line-height: 1.8;
          font-weight: 700;
          text-transform: uppercase;
          &:hover{
            text-decoration: underline;
          }
        }
        font-weight: 400;
        letter-spacing: .022rem;

        > ul {
          > li {}
        }
      }
    }
  }

  .col-group {
    width: calc(100%/6 * 3);
    @media (max-width: 979px) {
      // width: calc(100%/4 * 3);
      width: 100%;
    }
    @media (max-width: 767px) {
      width: 100%;
      // max-width: calc(100%/2);

    }

    .content-block-list {
      display: flex;
      flex-wrap: wrap;
      @media (max-width: 480px) {
        flex-direction: column;
      }

      .row-item {
        width: calc(100% / 3);
        @media (max-width: 767px) {
          width: unset;
          padding-right: 2rem;
          margin-bottom: 2rem;

          min-width: calc(100%/3);
        }
      }
    }
  }

  .col {
    width: calc(100%/6);
    @media (max-width: 979px) {
      width: calc(100%/4);
    }
    @media (max-width: 767px) {
      width: unset;
      min-width: calc(100%/3);
      // max-width: calc(100%/2);
      padding-right: 1rem;
      margin-bottom: 1rem;
    }

    li {
      margin-top: .2rem;
    }
  }

  .col-contact {
    width: calc(100%/6);
    @media (max-width: 979px) {
      width: calc(100%/2.5);
      margin-left: calc(100%/5);
      order: -1;
      margin-bottom: .5rem;
    }
    @media (max-width: 767px) {
      width: calc(100%/1.5);
      margin-left: 0;
      margin-right: calc(100%/3);
    }
  }
}
