section.calculators {
  .container {
    padding-bottom: 4rem;
    padding-top: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 991px) {
      flex-direction: column;
    }
  }

  .mc-label {
    // flex-grow: 1;

    // width: 10.125rem;
    // height: 3.813rem;
    // color: #2759b8;
    color: $theme1;
    font-family: 'Roboto', sans-serif;
    font-size: 1.875rem;
    font-weight: 800;
    line-height: 1.33;
    letter-spacing: .087rem;
    @media (max-width: 991px) {
      padding-bottom: 2rem;
      font-size: 2rem;
    }
  }

  li,
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .mc-list {
    font-size: .9rem;
    @media (min-width: 992px) {
      margin-left: auto;
      margin-right: 0;
      padding-left: 1rem;
    }
    @media (max-width: 700px) {
      width: 90%;
      flex-direction: column;
    }
    display: flex;

    li {
      text-align: center;
      @media (min-width: 700px) {

        padding: 0 2rem;

        // &:first-child {
        //   padding-left: 0;
        // }

        // &:last-child {
        //   padding-right: 0;
        // }
      }
      padding: 0 1rem 2rem;

      &:last-child {
        padding-bottom: 0;
      }

      > * {
        margin-top: .5em;

        &:first-child {
          margin-top: 0;
        }
      }

      img {}

      h6 {
        text-transform: uppercase;
        color: $dark;
        font-weight: 700;
      }

      .mc-text {
        * {
          margin: 0;
        }
        text-align: left;
      }
    }
  }
  @media (max-width: 991px) {
    .arrow {
      margin-top: .5rem;

      &:before {
        transform: rotate(90deg);
      }

      &:hover {
        &:before {
          height: 0;
          width: 2.688rem;
        }
      }
    }
  }
}

.calc-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;

  .item {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: calc(80% / 4);
    margin-left: calc(20% / 4);
    line-height: 1.84;
    @media (max-width: 991px) {
      width: calc(90% / 3);
      margin-left: calc(10% / 3);
    }
    @media (max-width: 767px) {
      width: calc(80% / 2);
      margin-left: calc(15% / 2);
    }
    @media (max-width: 480px) {
      width: 80%;
      margin-left: 10%;
    }

    h6 {
      text-align: center;
      width: 100%;
      text-transform: uppercase;
      margin: 1.2rem 0 .7rem;

      a {
        color: $dark;

        &:hover {
          color: $dark;
        }
      }
    }

    img {
      display: block;
      margin: 0 auto;
    }
  }

  &:after {
    display: block;
    content: '';
    width: 100%;
    height: .05rem;
    min-height: 1px;
    margin-top: 1.3rem;
    margin-bottom: 1.3rem;
    background: $theme2;
  }

  &:last-child {
    &:after {
      display: none;
    }
  }
}

.row {
  > .col-md-12 {
    > h3 {
      margin: 1.5rem 0 1rem;
    }
  }
}
