.about-lo {
  padding-top: 6rem;
  padding-bottom: 6rem;
  display: flex;
  // align-items: center;
  width: 80%;

  margin: 0 auto;

  .img {
    width: 45%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .square-box {
      width: calc(100% - 3rem);
      display: block;
      margin: .5rem auto 0 0;
    }
  }

  .content {
    width: 55%;

    h1,
    h3 {
      margin-bottom: .3em;
    }

    .btn-controls {
      margin-top: 1rem;
    }
  }
  @media (max-width: 979px) {
    width: 100%;
    
  padding-top: 4rem;
  padding-bottom: 4rem;
  }
  @media (max-width: 767px) {
    flex-direction: column;

    .content,
    .img {
      width: 100%;
    }

    .img {
      .square-box {
        width: 50%;
      }
    }

    .content {
      h1 {
        margin-top: .2em;
      }
    }
  }
  @media (max-width: 480px) {
    .img {
        .square-box {
          width: 80%;
          margin: 0 auto;
        }
      }
  }
}
