.socials {
  margin: 0;
  padding: 0;
  color: $light;
  list-style: none;

  li {
    margin: 0;
    padding: 0;
  }

  a {
    padding: .2rem;
    display: block;
  }

  display: flex;
  flex-wrap: wrap;
}

.socialicon {
  width: 20px;
  height: 20px;
  margin: 3px;
  background-size: contain;
  background-position: 50% 50%;
  background-color: transparent;
  background-repeat: no-repeat;
  display: block;
  opacity: 0.9;

  &:hover{
    opacity: 0.6;
  }
  
  &:active,
  &:focus{
    opacity: 1;
  }
 
  &-twitter {background-image: url($imgPath + 'social_twitter.png');}
  &-facebook {background-image: url($imgPath + 'social_facebook.png');}
  &-linkedin {background-image: url($imgPath + 'social_linkedin.png');}
  &-youtube {background-image: url($imgPath + 'social_youtube.png');}
  &-instagram {background-image: url($imgPath + 'social_instagram.png');}
  &-phone {background-image: url($imgPath + 'icon_phone.png');}

  &-twitter_d {background-image: url($imgPath + 'social_twitter_d.png');}
  &-facebook_d {background-image: url($imgPath + 'social_facebook_d.png');}
  &-linkedin_d {background-image: url($imgPath + 'social_linkedin_d.png');}
  &-youtube_d {background-image: url($imgPath + 'social_youtube_d.png');}
  &-instagram_d {background-image: url($imgPath + 'social_instagram_d.png');}
  &-phone_d {background-image: url($imgPath + 'icon_phone_d.png');}
}
