.contact_form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .contact-item {
    width: 30%;

    &.long {
      width: 100%;
    }
  }

  h6 {
    width: 100%;
  }

  .form-item {
    width: 45%;
    @media (max-width: 480px) {
      width: 100%;
    }

    label {
      width: 100%;
      margin-top: .8rem;
      color: $dark;
      text-transform: uppercase;
      margin-bottom: 0;
      display: inline-block;
    }

    input,
    textarea {
      width: 100%;
      resize: vertical;
      border: 3px solid $dark;
      background: transparent;
      padding: .2rem;

      &:active,
      &:focus {
        outline: none;
        background: rgba($light,.7);
      }
    }

    textarea {
      line-height: 1.5rem;
      min-height: calc(4.5rem + .4rem + 6px);
    }

    &.long {
      width: 100%;
      margin-right: 0;
    }
  }

  .btn-empty_d {
    margin: .7rem 0 0 auto;
  }
}
