.logo {
  img {
    width: 100%;
    object-fit: contain;
    object-position: 0 50%;
    max-width: 13rem;
    max-height: 4rem;
  }
}

.logo-responsive {
  img {
    min-height: 2.5rem;
    min-width: 3.5rem;
    @media (min-width: 980px) and (max-width:1200px ) {
      width: 5rem;
      background-repeat: no-repeat;
      background-size: contain;
      object-position: 0 500%;
      background-position: 50% 50%;
      background-image: url($imgPath + 'GEM-Logo_m.png');
      // background-image: url($imgPath + 'GEM-Logo_m.png');
    }
    @media (max-width: 650px) {
      width: 8rem;
      background-repeat: no-repeat;
      background-size: contain;
      object-position: 0 500%;
      background-position: 50% 50%;
      background-image: url($imgPath + 'GEM-Logo_m.png');
      // background-image: url($imgPath + 'GEM-Logo_m.png');
    }
    @media (max-width: 480px) {
      width: 5rem;

      background-image: url($imgPath + 'GEM-Logo_s.png');
      // background-image: url($imgPath + 'GEM-Logo_s.png');
    }
  }
}

a {
  color: inherit;

  &:hover {
    color: inherit;
    opacity: .9;
  }
}

%line {
  &:after {
    left: 0;
    background: $theme1;
    display: block;
    content: '';
    position: absolute;
    width: 100%;
    max-width: 100vw;
    height: .2rem;
  }

  &_before {
    &:before {
      left: 0;
      background: $theme1;
      display: block;
      content: '';
      position: absolute;
      max-width: 100vw;
      width: 100%;
      height: .2rem;
    }
  }
}

%acive_item {
  li {
    display: none;

    &.active {
      display: block;
    }
  }
}

%bg-image {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: $theme1;
  position: absolute;
  z-index: -1;

  li {
    display: block;
  }

  * {
    position: static;
  }

  img {
    height: 100%;
    width: 100%;
    opacity: .8;
    object-fit: cover;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
  }
  @extend %acive_item;
}

.inner_page {
  padding-top: 4rem;
  padding-bottom: 4rem;

  h1 {
    margin-bottom: .9rem;
  }

  p {
    margin-bottom: 1em;
  }
}

.arrow {
  display: block;
  width: fit-content;
  margin: auto;
  position: relative;
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  padding: 1rem 0;
  color: transparent;
  height: fit-content;
  white-space: nowrap;

  a {
    color: transparent;
    text-decoration: none;
    transition: all .2s ease-in-out;
  }

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -1rem;
    margin-left: -1.344rem;
    display: block;
    width: 2.688rem;
    height: 2rem;
    background: url($imgPath + 'Arrow.png');
    background-position: 50% 50%;
    background-repeat: no-repeat;
    transition: all .2s ease-in-out;
  }
  transition: all .2s ease-in-out;

  &.down {
    &:before {
      transform: rotate(90deg);
    }

    &:hover {
      &:before {
        height: 0;
        width: 2.688rem;
      }
    }
  }

  &:hover {
    color: $theme1;
    a{
      color: $theme1;
    }

    &:before {
      opacity: 0;
      width: 0;
    }
  }
}

.square-box {
  position: relative;
  overflow: hidden;
  // width: 50%;
  // background: #4679BD;
  &:before {
    content: "";
    display: block;
    padding-top: 100%;
  }

  .square-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    // color: white;
    // text-align: center;
  }
}

.btn-controls,
.btn-group {
  margin-top: 1rem;

  .btn-custom,
  .btn-custom_d,
  .btn-empty,
  .btn-empty_d {
    margin: .3rem .5rem 0 0;

    &:last-child {
      margin-right: 0;
    }
  }
}
