.slider {
  .slick-dots {
    opacity: 0;
    pointer-events: none;
    position: fixed;
    z-index: -1000000;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $light;
  }

  li,
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  position: relative;
  z-index: 1;

  .bg-image {
    @extend %bg-image;
    @extend %acive_item;
  }

  .container {
    &.hiddenWhileScroll {
      opacity: 0;
      pointer-events: none;
      transition: all ease-in-out .4s;
    }
    
    padding-top: 13rem;
    padding-bottom: 9rem;
    @media (max-width: 767px) {
      padding-top: 8rem;
      padding-bottom: 4rem;
    }

    h1 {
      font-size: 3.125rem;
      margin-bottom: 0;
      line-height: 1.4;
    }

    span.text {
      line-height: 1.4;
      color: $light;
      margin-top: .3rem;
      margin-bottom: 1.5rem;
      display: block;
      font-size: 1.063rem;
      font-family: 'Open Sans', sans-serif;
      font-weight: 600;
    }
  }

  .slider-controls {
    display: inline-block;
    margin-right: .5rem;

    li {
      &.active {
        background: rgba($light,.35);
      }
      color: $light;
      cursor: pointer;
      display: inline-block;
      width: 3.6rem;
      height: 3.6rem;
      line-height: 3rem;
      font-size: 1.1rem;
      border-radius: 50%;
      text-align: center;
      border: .3rem solid $light;
      position: relative;
      margin-left: 1rem;

      &:before {
        cursor: default;
        position: absolute;
        content: '';
        width: 1.8rem;
        height: 4px;
        background: $light;
        right: 100%;
        top: 50%;
        margin-top: -0.1rem;
      }

      &:first-child {
        margin-left: 0;

        &:before {
          display: none;
        }
      }
    }
  }

  .step {
    display: inline-block;
    @extend %acive_item;
    vertical-align: bottom;
    margin-bottom: .3rem;

    font-weight: 900;
    font-size: 1.688rem;
    line-height: 1.86;
    letter-spacing: .073rem;
    @media (max-width: 767px) {
      display: block;
    }
  }

  .slider-icon {
    @extend %acive_item;
    margin-top: 1rem;
  }
  @extend %line;
  @extend %line_before;

  &:before {
    z-index: 1;
    top: 4.4rem;
  }

  &:after {
    z-index: 0;
    bottom: 1rem;
    @media (max-width: 767px) {
      display: none;
    }
  }
}

.slides-body {
  .slick-slide {
    position: relative;
  }
}

.branch-header,
.lo-header {
  padding-top: 7rem;
  padding-bottom: 3rem;
  background: url($imgPath + 'branch_header.jpg');
  background-position: 0 0;
  background-size: cover;

  h1 {
    color: $light;
    // font-size: 3.125rem;
    line-height: 1.2;
    letter-spacing: .084rem;
    margin-bottom: .3em;
  }

  .text {
    color: $light;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.063rem;
    font-weight: 600;
    line-height: 1.32;
    letter-spacing: .022rem;

    span {
      &:before {
        content: ' | ';
      }

      &:first-child {
        &:before {
          display: none;
        }
      }
    }
  }

  position: relative;
  @extend %line;
  @extend %line_before;

  &:before {
    z-index: 1;
    top: 4.4rem;
  }

  &:after {
    z-index: 0;
    bottom: .5rem;
  }
}
